import React, { useState } from 'react';
import { Button, Form, Input, Space, List as UiList, Flex, Card } from 'antd';


export function List() {
	const [urls, setUrls] = useState<string[]>([]);
	const [telegramUrl, setTelegramUrl] = useState<string>('');

	const addTelegramUrl = () => {
		setUrls([...urls, telegramUrl]);
		setTelegramUrl('');
	}

	return (
		<Form name="listform" style={{ maxWidth: 900 }}>
			<Card>
				<Space align="start">
					<Space direction="vertical">
						<UiList
							size="default"
							bordered
							dataSource={ urls }
							renderItem={ (item) => (
								<UiList.Item >
									{ item }
								</UiList.Item>
							) }
						/>

						<Form.Item name="note">
							<Space>
								<Input
									onChange={ (event) => setTelegramUrl(event.target.value) }
									value={ telegramUrl }
								/>
								<Button
									type="default"
									onClick={ addTelegramUrl }
								>
									Add
								</Button>
							</Space>
						</Form.Item>
					</Space>

					<Space direction="vertical">
						<div>
							 
						</div>
						<div>
							
						</div>
					</Space>
				</Space>
				<div>
					<Button
						size="large"
						type="primary"
						onClick={ addTelegramUrl }
					>
						Save
					</Button>
				</div>
			</Card>
		</Form>
	)
}
